
.skills-icons{
    display: flex;
    justify-content: center;
    gap: 10rem;
}

.skills-icons img{
    width: 200px;
    height: 200px;
    margin-right: 2rem;
    margin-bottom: 2rem;
}



@media only screen and (max-width: 1100px) {
   
    .skills-icons{
        display: block;
    }

    .skills-icons img{
        width: 100px;
        height: 100px;
    }
}